import { Component, OnInit } from '@angular/core';
import {ManagementService} from '../../services/management/management.service';
import {EmployeeService} from '../../services/employee/employee.service';
import { environment } from '../../../environments/environment';


@Component({
  selector: 'app-messervices',
  templateUrl: './messervices.component.html',
  styleUrls: ['./messervices.component.scss']
})
export class MesservicesComponent implements OnInit {
  uri = environment.baseUrl;
  awsUrl = environment.awsUrl;
  public users :any ;
  public services : any ;
  public user : any ; 
  public superId : any ;
  constructor(
    private employeeService : EmployeeService,
    private mService : ManagementService,
  ) { }

  ngOnInit() {
    this.fetchServices();
    this.fetchUsers();
  }

  fetchServices(){
      this.mService.getServices()
      .subscribe(res => {
        this.services =res.filter(service => service.client == this.employeeService.getIdfromToken()) ;
        this.superId = this.services[0].clientConsult ;
        this.employeeService.getOneUser(this.superId)
        .subscribe(res => {
          this.user = res ;
          this.user.image = this.awsUrl + res.image ;
          console.log(this.user);
        }, err => {
          console.log(err);
        });
      }, err => {
        console.log(err);
      });
    }

    checkSuper(id){
      this.employeeService.getOneUser(id)
      .subscribe(res => {
        this.user = res ;
        this.user.image = this.awsUrl + res.image ;
        
      }, err => {
        console.log(err);
      });
    }

  fetchUsers(){
    this.employeeService.getUsers()
    .subscribe(res => {
      this.users = res ;
    }, err => {
      console.log(err);
    });
  }
  avenceService(tasks){
    let completed  = [] ;
    let all = [] ;
    tasks.forEach(one => {
        if(one.status == 'completed')
        completed.push(one);
        all.push(one);
    });
    return (completed.length/all.length)* 100 ;
  }
  
  stringAsDate(dateStr: string) {
    return new Date(dateStr);
  }

}
